"use client";

import { ChevronDown, User } from "lucide-react";
import Link from "next/link";
import { useState } from "react";

export default function Navbar() {
  const menus = [
    { title: "UMROH & HAJI", href: "/umroh-haji" },
    { title: "SARIMBIT", href: "/sarimbit" },
    { title: "PERLENGKAPAN SALAT", href: "/perlengkapan-salat" },
    { title: "PALESTINE SCARF", href: "/palestine-scarf" },
    { title: "NEW PRODUCT", href: "/new-product" },
    {
      title: "PAKAIAN",
      href: "/pakaian",
      submenu: [
        { title: "Kemeja", href: "/pakaian/kemeja" },
        { title: "Tunik", href: "/pakaian/tunik" },
        { title: "Gamis", href: "/pakaian/gamis" },
      ],
    },
    { title: "SCARF", href: "/scarf" },
    { title: "ZATTA MEN", href: "/zatta-men" },
    { title: "ELZATTA KIDS", href: "/elzatta-kids" },
    { title: "INSTAN HIJAB", href: "/instan-hijab" },
  ];

  const [hoveredMenu, setHoveredMenu] = useState<string | null>(null);

  return (
    <header className="border-b border-gray-200">
      <div className="flex justify-between items-center max-w-7xl mx-auto px-6 py-3">
        <div className="flex-1" />

        <Link href="/" className="flex justify-center">
          <span className="font-semibold text-2xl text-[#4B1E32] tracking-wide">
            elzatta
          </span>
        </Link>

        <div className="flex-1 flex justify-end items-center space-x-5 text-gray-700">
          <User className="w-5 h-5 cursor-pointer hover:text-black" />
        </div>
      </div>

      <nav className="relative flex justify-center items-center space-x-7 text-xs tracking-wide uppercase font-medium text-gray-800 py-2">
        {menus.map((menu) => (
          <div
            key={menu.title}
            className="relative group"
            onMouseEnter={() => setHoveredMenu(menu.title)}
            onMouseLeave={() => setHoveredMenu(null)}
          >
            <Link href={menu.href} className="flex items-center gap-1 relative">
              <span className="hover:text-black transition-colors flex items-center gap-1">
                {menu.title}
                {menu.submenu && <ChevronDown className="w-3 h-3 mt-[1px]" />}
              </span>
              <span className="absolute left-0 -bottom-[2px] h-[2px] w-0 bg-black transition-all duration-300 group-hover:w-full"></span>
            </Link>

            {/* Dropdown  */}
            {menu.submenu && hoveredMenu === menu.title && (
              <div className="absolute left-0 mt-2 bg-white border border-gray-200 shadow-sm rounded-none py-2 min-w-[150px] z-50">
                {menu.submenu.map((sub) => (
                  <Link
                    key={sub.title}
                    href={sub.href}
                    className="block px-4 py-2 text-sm hover:bg-gray-50 hover:text-[#4B1E32] transition-colors"
                  >
                    {sub.title}
                  </Link>
                ))}
              </div>
            )}
          </div>
        ))}
      </nav>
    </header>
  );
}
